body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
	box-sizing: border-box;
}

body {
	background-color: #eff3f7;
	margin: 0;
	padding: 0;
	height: 100%;
}

html, body, #root, .container {
	height: 100%;
	display: flex;
	justify-content: center;
}

.loader {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: bold;
}

.movies {
	display: flex;
	justify-content: space-between;
align-items: flex-start;
flex-wrap: wrap;
padding: 50px;
padding-top: 70px;
width: calc(100% - 1 / 2);
}

.movie {
	display: flex;
	flex-direction: column;
	align-self: stretch;
	width: 45%;
	background-color: #fff;
	align-items: flex-start;
	margin-bottom: 70px;
	justify-content: space-between;
	font-weight: 30;
	padding: 20px;
	border-radius: 5px;
	color:#adaeb9;
	box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25),
	0 8px 16px -8px rgba(0, 0, 0, 0.3),
	 0 -6px 16px -6px rgba(0,0,0, 0.25);
}

.movie img{
position: relative;
min-width: 120px;
top: -50px;
margin-right: 30px;
box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25),
	0 8px 16px -8px rgba(0, 0, 0, 0.3),
	 0 -6px 16px -6px rgba(0,0,0, 0.25);
}

.movie .movie__title,
.movie .movie__year {
	color:#2c2c2c;
	font-weight: bold;
	margin: 0;
}

.movie .movie__title{
	margin-bottom: 5px;
	font-size: 24px;
	color: #2c2c2c;
}

.movie .movie__genres , .movie .torrents {
	list-style: none;
	padding: 0;
	margin: 5px 0;
	display: flex;
	flex-wrap: wrap;
}

.movie__genres li,
.movie .movie__year {
	font-size: 14px;
	margin-right: 10px;
}

.movie .movie__column {
	display: flex;
	flex-direction: column;
	height: 100%;
}

.torrents {
	justify-content: space-around;
}

.torrents__torent > a{
	font-size: 18px;
	text-decoration: none;
	font-weight: bold;
}

.movie .movie__summary {
	flex-grow: 1;
	align-self: auto;
}

